<template>
	<div class="layout--main" :class="[navbarClasses, footerClasses]">
		
		<the-customizer @updateNavbar="updateNavbar" @updateNavbarColor="updateNavbarColor" :navbarType="navbarType" :navbarColor="navbarColor" :footerType="footerType" @updateFooter="updateFooter" :routerTransition="routerTransition" @updateRouterTransition="updateRouterTransition" v-if="!disableCustomizer" />
		<vx-sidebar :sidebarItems="sidebarItems" :logo="require('@/assets/images/logo/logo.png')" title="Vuesax" parent=".layout--main" />

		<div id="content-area" :class="[contentAreaClass, {'show-overlay': bodyOverlay}]">

			<div id="content-overlay"></div>

			<div class="content-wrapper">

				<the-navbar :navbarColor="navbarColor" :class="[{'text-white': isNavbarDark && !isThemeDark}, {'text-base': !isNavbarDark && isThemeDark}]" />

				<div class="router-view">
					<div class="router-content" :class="{'mt-0': navbarType == 'hidden'}">
						<transition :name="routerTransition">
						<div class="router-header flex flex-wrap items-center mb-6" v-if="$route.meta.breadcrumb || $route.meta.pageTitle">
							<div class="content-area__heading" :class="{'pr-4 border-0 md:border-r border-t-0 border-b-0 border-l-0 border-solid border-grey-light' : $route.meta.breadcrumb}">
								<h2 class="mb-1">{{ routeTitle }}</h2>
							</div>
							<vx-breadcrumb class="ml-4 md:block hidden" v-if="$route.meta.breadcrumb" />
							<vs-dropdown class="ml-auto md:block hidden" vs-trigger-click>
								<vs-button radius icon="icon-settings" icon-pack="feather"></vs-button>

								<vs-dropdown-menu class="w-32">
									<vs-dropdown-item><router-link to="/pages/profile" class="flex items-center"><feather-icon icon="UserIcon" class="inline-block mr-2" svgClasses="w-4 h-4" /><span>Profile</span></router-link></vs-dropdown-item>
									<vs-dropdown-item><router-link to="/apps/todo" class="flex items-center"><feather-icon icon="CheckSquareIcon" class="inline-block mr-2" svgClasses="w-4 h-4" /><span>Tasks</span></router-link></vs-dropdown-item>
									<vs-dropdown-item><router-link to="/apps/email" class="flex items-center"><feather-icon icon="MailIcon" class="inline-block mr-2" svgClasses="w-4 h-4" /><span>Inbox</span></router-link></vs-dropdown-item>
								</vs-dropdown-menu>
							</vs-dropdown>
						</div>
						</transition>
						<div class="content-area__content">
							<transition :name="routerTransition" mode="out-in">
								<router-view @changeRouteTitle="changeRouteTitle"></router-view>
							</transition>
						</div>
					</div>
				</div>

			</div>

			<the-footer></the-footer>
		</div>
	</div>
</template>

<script>
import VxSidebar from "../components/vx-sidebar/VxSidebar.vue";
import TheCustomizer from "../components/customizer/TheCustomizer.vue";
import TheNavbar from '../components/TheNavbar.vue';
import TheFooter from '../components/TheFooter.vue';
import themeConfig from '@/../themeConfig.js';
import sidebarItems from "@/layouts/components/vx-sidebar/sidebarItems.js";

export default {
	data() {
		return {
			navbarType: themeConfig.navbarType || 'floating',
			navbarColor: themeConfig.navbarColor || '#fff',
			footerType: themeConfig.footerType || 'static',
			routerTransition: themeConfig.routerTransition || 'none',
			isNavbarDark: false,
			routeTitle: this.$route.meta.pageTitle,
			sidebarItems: sidebarItems,
			disableCustomizer: themeConfig.disableCustomizer,
			windowWidth: window.innerWidth, //width of windows
		}
	},
	watch: {
		'$route'() {
			this.routeTitle = this.$route.meta.pageTitle;
		},
		isThemeDark(val) {
			if(this.navbarColor == "#fff" && val) {
				this.updateNavbarColor("#10163a")
			}else {
				this.updateNavbarColor("#fff")
			}
		},
	},
	computed: {
		isThemeDark() { return this.$store.state.isDark },
		sidebarWidth() {
			return this.$store.state.sidebarWidth;
		},
		bodyOverlay() {
			return this.$store.state.bodyOverlay;
		},
		contentAreaClass() {
			if(this.sidebarWidth == "default") return "content-area-default"
			else if(this.sidebarWidth == "reduced") return "content-area-reduced"
			else if(this.sidebarWidth) return "content-area-full"
		},
		navbarClasses() {
			return {
				'navbar-hidden': this.navbarType == 'hidden',
				'navbar-sticky': this.navbarType == 'sticky',
				'navbar-static': this.navbarType == 'static',
				'navbar-floating': this.navbarType == 'floating',
			}
		},
		footerClasses() {
			return {
				'footer-hidden': this.footerType == 'hidden',
				'footer-sticky': this.footerType == 'sticky',
				'footer-static': this.footerType == 'static',
			}
		},
	},
	methods: {
		changeRouteTitle(title) {
			this.routeTitle = title;
		},
		updateNavbar(val) {
			this.navbarType = val;
		},
		updateNavbarColor(val) {
			this.navbarColor = val;
			if(val == "#fff") this.isNavbarDark = false
			else this.isNavbarDark = true
		},
		updateFooter(val) {
			this.footerType = val;
		},
		updateRouterTransition(val) {
			this.routerTransition = val;
		},
		handleWindowResize(event) {
			this.windowWidth = event.currentTarget.innerWidth;
			this.setSidebarWidth();
		},
		setSidebarWidth() {
			if (this.windowWidth < 1200) {
				this.$store.commit('TOGGLE_IS_SIDEBAR_ACTIVE', false)
				this.$store.dispatch('updateSidebarWidth', 'no-sidebar')
			}
			else if(this.windowWidth < 1200) {
				this.$store.dispatch('updateSidebarWidth', 'reduced')
			}
			else {
				this.$store.commit('TOGGLE_IS_SIDEBAR_ACTIVE', true)
			}
		}
	},
	created() {
		this.setSidebarWidth();
		if(this.navbarColor == "#fff" && this.isThemeDark) {
			this.updateNavbarColor("#10163a")
		}else {
			this.updateNavbarColor(this.navbarColor)
		}
	},
	components: {
		VxSidebar,
		TheNavbar,
		TheFooter,
		TheCustomizer,
	},

}
</script>

<style>
.zoom-fade-enter-active,
.zoom-fade-leave-active {
  transition: transform .35s, opacity .28s ease-in-out;
}
.zoom-fade-enter {
	transform: scale(0.97);
  opacity: 0;
}

.zoom-fade-leave-to {
	transform: scale(1.03);
	opacity: 0;
}

/* FADE TRNASITION */
.fade-enter-active,
.fade-leave-active {
  transition: opacity .28s ease-in-out;
}

.fade-enter,
.fade-leave-to {
	opacity: 0;
}

/* PAGE SLIDE */
.slide-fade-enter-active, .slide-fade-leave-active {
  transition: opacity .35s, transform .4s;
}
.slide-fade-enter {
  opacity: 0;
  transform: translateX(-30%);
}

.slide-fade-leave-to{
	opacity: 0;
	transform: translateX(30%);
}

/* ZOOM OUT */
.zoom-out-enter-active, .zoom-out-leave-active {
  transition: opacity .35s ease-in-out, transform .45s ease-out;
}
.zoom-out-enter, .zoom-out-leave-to {
  opacity: 0;
  transform: scale(0);
}

/* UNFOLD SLIDE */
.fade-bottom-enter-active, .fade-bottom-leave-active {
  transition: opacity .3s, transform .35s;
}
.fade-bottom-enter {
  opacity: 0;
  transform: translateY(-8%);
}

.fade-bottom-leave-to{
	opacity: 0;
	transform: translateY(8%);
}
</style>