<template>
<div class="relative">
	<div class="vx-navbar-wrapper">
		<vs-navbar class="vx-navbar navbar-custom" :color="navbarColor" :class="classObj">

			<!-- SM - OPEN SIDEBAR BUTTON -->
			<feather-icon class="sm:inline-flex xl:hidden cursor-pointer mr-1" icon="AlignJustifyIcon" @click.stop="showSidebar"></feather-icon>

		


			<vs-spacer></vs-spacer>


			<!-- NOTIFICATIONS -->
			<vs-dropdown vs-custom-content vs-trigger-click>
				<feather-icon icon="BellIcon" class="cursor-pointer mx-6 mt-1" :badge="unreadNotifications.length"></feather-icon>
				<vs-dropdown-menu class="notification-dropdown">
					<div class="notification-top text-center p-5 bg-primary text-white">
						<h3 class="text-white">{{ unreadNotifications.length }} New</h3>
						<p class="opacity-75">App Notifications</p>
					</div>
					<VuePerfectScrollbar ref="mainSidebarPs" class="scroll-area--nofications p-0" :settings="settings">
					<ul class="bordered-items">
						<li v-for="ntf in unreadNotifications" :key="ntf.index" class="flex justify-between px-4 py-4 notification cursor-pointer">
							<div class="flex items-start">
								<feather-icon :icon="ntf.icon" :svgClasses="[`text-${ntf.category}`, 'stroke-current mr-1 h-6 w-6']"></feather-icon>
								<div class="mx-2">
									<span class="font-medium block notification-title" :class="[`text-${ntf.category}`]">{{ ntf.title }}</span>
									<small>{{ ntf.msg }}</small>
								</div>
							</div>
							<small class="mt-1 whitespace-no-wrap">{{ elapsedTime(ntf.time) }}</small>
						</li>
						<li class="flex justify-around p-3 notification cursor-pointer">
							<small>View All Notifications</small>
						</li>
					</ul>
					</VuePerfectScrollbar>
				</vs-dropdown-menu>
			</vs-dropdown>

			<!-- SEARCHBAR -->
			<div class="search-full-container w-full h-full absolute pin-l rounded-lg" :class="{'flex': showFullSearch}" v-show="showFullSearch">
				<vx-auto-suggest :autoFocus="showFullSearch" :data="navbarSearchAndPinList" @selected="selected" ref="navbarSearch" @closeSearchbar="showFullSearch = false" placeholder="Search..." class="w-full" inputClassses="w-full input-no-border no-icon-border" icon="SearchIcon" background-overlay></vx-auto-suggest>
				<feather-icon icon="XIcon" class="px-4 cursor-pointer pin-r h-full close-search-icon" @click="showFullSearch = false"></feather-icon>
			</div>
			<feather-icon icon="SearchIcon" @click="showFullSearch = true" class="cursor-pointer"></feather-icon>

			<!-- USER META -->
			<div class="the-navbar__user-meta flex items-center sm:ml-5 ml-2">
				<div class="text-right leading-tight hidden sm:block">
					<p class="font-semibold">Horpey Eureka</p>
				</div>
				<vs-dropdown vs-custom-content vs-trigger-click>
					<div class="con-img ml-3"><img :src="require(`@/assets/images/portrait/small/${activeUserInfo.img}`)" alt="" width="40" height="40" class="rounded-full shadow-md cursor-pointer block"></div>
					<vs-dropdown-menu>
						<ul style="min-width: 9rem">
							<li class="flex py-2 px-4 cursor-pointer hover:bg-primary hover:text-white" @click="$router.push('/pages/profile')"><feather-icon icon="UserIcon" svgClasses="w-4 h-4"></feather-icon> <span class="ml-2">Profile</span></li>
							<li class="flex py-2 px-4 cursor-pointer hover:bg-primary hover:text-white" @click="$router.push('/apps/email')"><feather-icon icon="MailIcon" svgClasses="w-4 h-4"></feather-icon> <span class="ml-2">Inbox</span></li>
							<li class="flex py-2 px-4 cursor-pointer hover:bg-primary hover:text-white" @click="$router.push('/apps/todo')"><feather-icon icon="CheckSquareIcon" svgClasses="w-4 h-4"></feather-icon> <span class="ml-2">Tasks</span></li>
							<li class="flex py-2 px-4 cursor-pointer hover:bg-primary hover:text-white" @click="$router.push('/apps/chat')"><feather-icon icon="MessageSquareIcon" svgClasses="w-4 h-4"></feather-icon> <span class="ml-2">Chat</span></li>
							<vs-divider class="m-1"></vs-divider>
							<li class="flex py-2 px-4 cursor-pointer hover:bg-primary hover:text-white" @click="$router.push('/pages/login')"><feather-icon icon="LogOutIcon" svgClasses="w-4 h-4"></feather-icon> <span class="ml-2">Logout</span></li>
						</ul>
					</vs-dropdown-menu>
				</vs-dropdown>
			</div>

		</vs-navbar>
	</div>
</div>
</template>

<script>
import VxAutoSuggest from '@/layouts/components/vx-auto-suggest/VxAutoSuggest.vue';
import VuePerfectScrollbar from 'vue-perfect-scrollbar'

export default {
    name: "the-navbar",
    props: {
        navbarColor: {
            type: String,
            default: "#fff",
        },
    },
    data() {
        return {
            navbarSearchAndPinList: this.$store.state.navbarSearchAndPinList,
            searchQuery: '',
            showFullSearch: false,
            unreadNotifications: [
                { index: 0, title: 'New Message', msg: 'Are your going to meet me tonight?', icon: 'MessageSquareIcon', time: 'Wed Jan 30 2019 07:45:23 GMT+0000 (GMT)', category: 'primary' },
                { index: 1, title: 'New Order Recieved', msg: 'You got new order of goods.', icon: 'PackageIcon', time: 'Wed Jan 30 2019 07:45:23 GMT+0000 (GMT)', category: 'success' },
                { index: 2, title: 'Server Limit Reached!', msg: 'Server have 99% CPU usage.', icon: 'AlertOctagonIcon', time: 'Thu Jan 31 2019 07:45:23 GMT+0000 (GMT)', category: 'danger' },
                { index: 3, title: 'New Mail From Peter', msg: 'Cake sesame snaps cupcake', icon: 'MailIcon', time: 'Fri Feb 01 2019 07:45:23 GMT+0000 (GMT)', category: 'primary' },
                { index: 4, title: 'Bruce\'s Party', msg: 'Chocolate cake oat cake tiramisu marzipan', icon: 'CalendarIcon', time: 'Fri Feb 02 2019 07:45:23 GMT+0000 (GMT)', category: 'warning' },
            ],
            settings: { // perfectscrollbar settings
                maxScrollbarLength: 60,
                wheelSpeed: .60,
            },
            autoFocusSearch: false,
            showBookmarkPagesDropdown: false,
        }
    },
    watch: {
        '$route'() {
            if (this.showBookmarkPagesDropdown) this.showBookmarkPagesDropdown = false
        }
    },
    computed: {
        data() {
            return this.$store.state.navbarSearchAndPinList;
        },
        activeUserInfo() {
            return this.$store.state.AppActiveUser;
        },
        sidebarWidth() {
            return this.$store.state.sidebarWidth;
        },
        breakpoint() {
            return this.$store.state.breakpoint;
        },
        classObj() {
            if (this.sidebarWidth == "default") return "navbar-default"
            else if (this.sidebarWidth == "reduced") return "navbar-reduced"
            else if (this.sidebarWidth) return "navbar-full"
        },
        getCurrentLocaleData() {
            const locale = this.$i18n.locale;
            if (locale == "en") return { flag: "us", lang: 'English' }
            else if (locale == "pt") return { flag: "br", lang: 'Portuguese' }
            else if (locale == "fr") return { flag: "fr", lang: 'French' }
            else if (locale == "de") return { flag: "de", lang: 'German' }
        },
        starredPages() {
            return this.$store.getters.starredPages;
        },
        starredPagesLimited() { return this.starredPages.slice(0, 10); },
        starredPagesMore() { return this.starredPages.slice(10); }
    },
    methods: {
        updateLocale(locale) {
            this.$i18n.locale = locale;
        },
        showSidebar() {
            this.$store.commit('TOGGLE_IS_SIDEBAR_ACTIVE', true);
        },
        selected(item) {
            this.$router.push(item.url)
            this.showFullSearch = false;
        },
        actionClicked(item) {
            // e.stopPropogation();
            this.$store.dispatch('updateStarredPage', { index: item.index, val: !item.hightLightAction });
        },
        showNavbarSearch() {
            this.showFullSearch = true;
        },
        showSearchbar() {
            this.showFullSearch = true;
        },
        elapsedTime(startTime) {
            let x = new Date(startTime);
            let now = new Date();
            var timeDiff = now - x;
            timeDiff /= 1000;

            var seconds = Math.round(timeDiff);
            timeDiff = Math.floor(timeDiff / 60);

            var minutes = Math.round(timeDiff % 60);
            timeDiff = Math.floor(timeDiff / 60);

            var hours = Math.round(timeDiff % 24);
            timeDiff = Math.floor(timeDiff / 24);

            var days = Math.round(timeDiff % 365);
            timeDiff = Math.floor(timeDiff / 365);

            var years = timeDiff;

            if (years > 0) {
                return years + (years > 1 ? ' Years ' : ' Year ') + 'ago';
            } else if (days > 0) {
                return days + (days > 1 ? ' Days ' : ' Day ') + 'ago';
            } else if (hours > 0) {
                return hours + (hours > 1 ? ' Hrs ' : ' Hour ') + 'ago';
            } else if (minutes > 0) {
                return minutes + (minutes > 1 ? ' Mins ' : ' Min ') + 'ago';
            } else if (seconds > 0) {
                return seconds + (seconds > 1 ? `${seconds} sec ago` : 'just now');
            }

            return 'Just Now'
        },
        test() {
            alert();
        },
        outside: function() {
            this.showBookmarkPagesDropdown = false
        },
    },
    directives: {
        'click-outside': {
            bind: function(el, binding, vNode) {
                if (typeof binding.value !== 'function') {
                    const compName = vNode.context.name
                    let warn = `[Vue-click-outside:] provided expression '${binding.expression}' is not a function, but has to be`
                    if (compName) { warn += `Found in component '${compName}'` }

                    console.warn(warn)
                }
                const bubble = binding.modifiers.bubble
                const handler = (e) => {
                    if (bubble || (!el.contains(e.target) && el !== e.target)) {
                        binding.value(e)
                    }
                }
                el.__vueClickOutside__ = handler
                document.addEventListener('click', handler)
            },

            unbind: function(el) {
                document.removeEventListener('click', el.__vueClickOutside__)
                el.__vueClickOutside__ = null

            }
        }
    },
    components: {
        VxAutoSuggest,
        VuePerfectScrollbar
    },
}
</script>

<style lang="scss">
.starred-page,
.starred-page--more {
	&:hover {
		color: rgba(var(--vs-primary), 1);
	}
}

.i18n-dropdown {
	@media screen and (max-width: 364px) {
		width: 95vw;
		left: 90vw !important;

		.vs-dropdown--menu--after {
			display: none;
		}
	}

}

.notification-dropdown {
	width: 365px;

	@media screen and (max-width: 500px) {
		width: 95vw;
		left: 97.5vw !important;

		.vs-dropdown--menu--after {
			display: none;
		}
	}

	.notification {
		&:hover {
			background-color: #f7f7f7;
		}
	}

	.vs-dropdown--custom {
		padding: 0 !important;
		border: 0;
		overflow: hidden;
		@apply shadow-lg;
	}

	.vs-dropdown--menu--after {
		background: rgba(var(--vs-primary), 1) !important;
		right: 1.6rem !important;
	}
}

.vx-navbar-wrapper {
	background: linear-gradient(to bottom, rgba(248, 248, 248, 0.95) 44%, rgba(248, 248, 248, 0.46) 73%, rgba(255, 255, 255, 0) 100%);
	padding: 2.2rem;
	padding-top: 1.3rem;
	background-repeat-x: repeat;
	z-index: 41000;
	width: calc(100% - 260px - 1.2rem);
	height: 103px;
	background-repeat-y: no-repeat;
	position: fixed;
	top: 0;

	.vx-navbar {
		.vs-con-items {
			width: 100%;

			.search-full-container {
				background: #fff;
			}
		}
	}
}
.content-area-reduced{
	.vx-navbar-wrapper{
		width: calc(100% - 80px);		
	}
	.vs-navbar.vx-navbar{
		width: calc(100% - 80px - 4.4rem);
	}
}

.show-overlay .vx-navbar-wrapper {
	background: linear-gradient(to bottom, rgba(44, 48, 60, 0.9) 44%, rgba(44, 48, 60, 0.43) 73%, rgba(44, 48, 60, 0) 100%);
}

.navbar-sticky {
	.vx-navbar-wrapper {
		padding: 0;
		background: none;

		.vx-navbar {
			width: calc(100% - 260px);
			border-radius: 0;
			padding: .8rem 2.2rem;
		}
	}
	.content-area-reduced{
		.vx-navbar-wrapper {
			.vx-navbar {
				width: calc(100% - 80px);
			}
		}
	}

	.router-view {
		.router-content {
			margin-top: 4.5rem;
		}
	}
}

.navbar-static {
	.vx-navbar-wrapper {
		padding: 0;
		background: none;
		position: relative;
		height: auto;
		width: 100%;

		.vx-navbar {
			width: 100%;
			border-radius: 0;
			padding: .8rem 2.2rem;
			background: transparent !important;
			box-shadow: none;
			position: relative;
		}
	}

	.router-view {
		.router-content {
			margin-top: 0;
		}
	}
}

.navbar-hidden {
	.vx-navbar-wrapper {
		display: none;
	}
}

.scroll-area--nofications {
	position: relative;
	margin: auto;
	width: 100%;
	height: 20.2rem;
}

.search-full-container {
	z-index: 50000;
	.vx-auto-suggest {
		>div {
			height: 100%;
		}

		input[type="text"],
		.input-span-placeholder {
			padding: 1.25rem 3rem;
		}

		.vs-input--icon.feather {
			top: 34%;
			left: 0.8rem;
		}
	}

	>div.feather-icon {
		position: absolute !important;
	}
}

// Bookmark Dropdown
.bookmark-dropdown {
	z-index: 41001;
}

// navbar update fixes
.navbar-custom {
	.vs-navbar--btn-responsive {
		display: none !important;
	}
	.vs-spacer {
		display: block !important;
	}
	.vs-con-items {
		display: flex !important;
		width: 100%;
	}
}

@media (max-width: 1200px) {
	.vx-navbar-wrapper {
		width: calc(100% - 1.2rem);
	}

	.navbar-sticky {
		.vx-navbar-wrapper {
			.vx-navbar {
				width: 100%;
				padding: .8rem 2.2rem;
			}
		}
	}

}

@media (max-width: 576px) {
	.vx-navbar-wrapper {
		padding: 1.2rem;

		.vx-navbar.navbar-full {
			width: calc(100% - 2.4rem);
		}
	}

	.navbar-sticky {
		.vx-navbar-wrapper {
			padding: 0;

			.vx-navbar {
				width: 100%;
				padding: .8rem 1.5rem;
			}
		}
	}

	.navbar-static {
		.vx-navbar-wrapper {
			.vx-navbar {
				width: 100%;
				padding: .8rem 1.5rem;
			}
		}

		.router-view{
			.router-content{
				margin-top: 0;
			}
		}
	}
}
</style>